*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

html {
  /*This defines what 1rem is*/
  font-size: 62.5%;  /*10px wanted, 16px default size so 62.5%*/
}

body {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 3rem;
}

button {
  border: 1px solid black;
  border-radius: 2px;
  background: white;
  cursor: pointer;
}

.main-container {
  width: 50%;
  min-width: 960px;
  margin: 100px auto;
  text-align: center;
}

.books-container {
  border: 1px solid black;
  border-radius: 2px;
  margin-bottom: 10px;
  overflow: hidden;
}

.book {
  font-weight: bold;
  padding: 12px;
  cursor: pointer;
  text-align: center;
}

.book button {
  margin: 0 5px;
}

.newbook__checkbox {
  display: none;
}

.newbook__button {
  background-color: rgb(245,25,255);
  height: 7rem;
  width: 7rem;
  position: fixed;
  top: 6rem;
  right: 6rem;
  border-radius: 50%;
  z-index: 2000;
  -webkit-box-shadow: 0 1rem 3rem rgba(0,0,0,.3);
          box-shadow: 0 1rem 3rem rgba(0,0,0,.3);
  text-align: center;
  cursor: pointer;
}

.newbook__background {
  height: 6rem;
  width: 6rem;
  border-radius: 50%;
  position: fixed;
  top: 6.5rem;
  right: 6.5rem;
  background-image: -webkit-radial-gradient(rgb(75,25,255), pink);
  background-image: -o-radial-gradient(rgb(75,25,255), pink);
  background-image: radial-gradient(rgb(75,25,255), pink);
  z-index: 1000;
  -webkit-transition: -webkit-transform .8s cubic-bezier(0.86, 0, 0.07, 1);
  transition: -webkit-transform .8s cubic-bezier(0.86, 0, 0.07, 1);
  -o-transition: transform .8s cubic-bezier(0.86, 0, 0.07, 1);
  transition: transform .8s cubic-bezier(0.86, 0, 0.07, 1);
  transition: transform .8s cubic-bezier(0.86, 0, 0.07, 1), -webkit-transform .8s cubic-bezier(0.86, 0, 0.07, 1);
}

.newbook__form-container {
  height: 100vh;
  position: fixed;
  top: 15rem;
  left: 0;
  z-index: 1500;
  width: 0;
  opacity: 0;
  -webkit-transition: all .8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  -o-transition: all .8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition: all .8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.newbook__form {
  display: inline-block;
  border-radius: 4px;
  text-align: left;
  background-color: rgb(248,248,248);
  padding: 5rem 10rem;
  -webkit-box-shadow: 0 1.5rem 4rem rgba(0,0,0,0.35);
          box-shadow: 0 1.5rem 4rem rgba(0,0,0,0.35);
}

.newbook__form-header {
  color: rgb(75,25,255);
  text-transform: uppercase;
  font-size: 3.5rem;
  margin-bottom: 4rem;
  letter-spacing: .1rem;
}

.newbook__form-input {
  border: none;
  color: rgba(0,0,0,.6);
  border-radius: 4px;
  font-size: 2rem;
  padding: 1rem 1.5rem;
  margin-bottom: 3rem;
}

.newbook__form-input:focus {
  outline: none;
}

.newbook__form-btn {
  font-size: 1.8rem;
  text-decoration: none;
  border: none;
  background-color:rgb(75,25,255);;
  color: white;
  border-radius: 20px;
  padding: 1rem 3rem;
  text-transform: uppercase;
  letter-spacing: .1rem;
  cursor: pointer;
  display: block;
  margin-top: 3.5rem;
  -webkit-box-shadow: 0 .7rem 1rem rgba(0,0,0,.2);
          box-shadow: 0 .7rem 1rem rgba(0,0,0,.2);
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
}

.newbook__form-btn:hover {
  -webkit-transform: translateY(-3px);
      -ms-transform: translateY(-3px);
          transform: translateY(-3px);
  -webkit-box-shadow: 0 1.3rem 3rem rgba(0,0,0,.2);
          box-shadow: 0 1.3rem 3rem rgba(0,0,0,.2);
}
.newbook__form-btn::after {
  -webkit-transform: scaleX(1.4) scaleY(1.6);
      -ms-transform: scaleX(1.4) scaleY(1.6);
          transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}

.newbook__form-btn:active,
.newbook__form-btn:focus {
  outline: none;
  -webkit-transform: translateY(-1px);
      -ms-transform: translateY(-1px);
          transform: translateY(-1px);
  -webkit-box-shadow: 0 .5rem 1rem rgba(0,0,0,.2);
          box-shadow: 0 .5rem 1rem rgba(0,0,0,.2);
}

/*FUNCTIONALITY*/

.newbook__checkbox:checked ~ .newbook__background {
  -webkit-transform: scale(105);
      -ms-transform: scale(105);
          transform: scale(105);
}

.newbook__checkbox:checked ~ .newbook__form-container {
  opacity: 1;
  width: 100%;
}

  /*ICON*/
.newbook__icon {
  position: relative;
  margin-top: 3.5rem;
}

.newbook__icon,
.newbook__icon::before {
  width: 3rem;
  height: 3px;
  background-color: white;
  display: inline-block;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
}

.newbook__icon::before {
  content: "";
  position: absolute;
  left: 0;  
}

.newbook__icon::before { 
  top: -.2rem;
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg); 
}

.newbook__button:hover .newbook__icon::before,
.newbook__button:hover .newbook__icon {
  width: 3.5rem;
}

.newbook__checkbox:checked + .newbook__button .newbook__icon::before {
  top: 0; 
}

.newbook__checkbox:checked + .newbook__button .newbook__icon {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

.newbook__form-radio-header {
  font-size: 2rem;
  color: rgb(75,25,255);
  text-transform: uppercase;
  letter-spacing: .1rem;
}

/* input */
.newbook__form-radio {
  display: none;
}

.newbook__form-radio-label--yes,
.newbook__form-radio-label--no {
  position: relative;
  display: inline-block;
  font-size: 2rem;
  text-transform: uppercase;
  color: rgba(0,0,0,.6);
  padding-left: 4rem;
  cursor: pointer;
  margin-top: 2.5rem;
  margin-right: 5rem;
}

.newbook__form-radio-btn--yes, 
.newbook__form-radio-btn--no {
  height: 3rem;
  width: 3rem;
  border: 5px solid rgb(75,25,255);
  border-radius: 50%;
  display: inline-block;
  position: absolute;
  left: 0;
  top: -.4rem;
}
 
.newbook__form-radio-btn--yes::after,
.newbook__form-radio-btn--no::after {
  content: "";
  display: block;
  height: 1.3rem;
  width: 1.3rem;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: rgb(75,25,255);
  opacity: 0;
  -webkit-transition: opacity .2s;
  -o-transition: opacity .2s;
  transition: opacity .2s;
}

.newbook__form-radio#readYes:checked ~ .newbook__form-radio-label--yes .newbook__form-radio-btn--yes::after {
  opacity: 1;
}

.newbook__form-radio#readNo:checked ~ .newbook__form-radio-label--no .newbook__form-radio-btn--no::after {
  opacity: 1;
}


